:root {
  /* Grayscale */
  --color-black: #131313;
  --color-white: #ffffff;
  --color-gray-900: #1a1a1a;
  --color-gray-800: #222222;
  --color-gray-700: #2c2c2c;
  --color-gray-600: #3a3a3a;
  --color-gray-500: #4c4c4c;
  --color-gray-400: #5e5e5e;
  --color-gray-300: #707070;
  --color-gray-200: #8a8a8a;
  --color-gray-100: #a5a5a5;
  /* Accent Colors */
  --color-accent: #d4ff2a;
  --color-accent-light: #e8ff6b;
  --color-accent-dark: #aacc1f;
  --color-accent-contrast: #1a1a1a;
  /* Semantic */
  --color-bg: var(--color-black);
  --color-surface: var(--color-gray-900);
  --color-border: var(--color-gray-700);
  --color-text: var(--color-white);
  --color-muted: var(--color-gray-200);
}

/*SCSS RESET*/
/* Box sizing reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles */
ul[role=list],
ol[role=list],
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

/* Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* Remove built-in form typography styles */
input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
  background: none;
  border: none;
  outline: none;
}

/* Remove animations and transitions for people who prefer reduced motion */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*---------------------------*/
body {
  background-color: var(--color-bg);
  color: var(--color-text);
  font-family: sans-serif;
}

/*ASSETS*/
/*TYPOGRAPHY*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #ffffff;
}

/*BTN*/
.btn-primary {
  display: inline-block;
  padding: 1rem 2rem;
  text-decoration: none;
  transition: background-color 0.3s ease;
  background-color: #d4ff2a;
  border-color: #d4ff2a;
  color: #131313;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1rem;
}
.btn-primary:hover {
  background-color: #aacc1f;
}

.btn-icon {
  font-weight: 700;
  font-size: 1rem;
  border: solid 1px #d4ff2a;
  padding: 1rem;
  border-radius: 99px;
  color: #d4ff2a;
}
.btn-icon:hover {
  background-color: #d4ff2a;
  color: #131313;
}

/*BADGE*/
.badge {
  display: inline;
  flex: 0 0 auto;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  text-transform: uppercase;
}

.badge-primary {
  background-color: #d4ff2a;
  border-color: #d4ff2a;
  color: #131313;
}

/*LAYOUT*/
section,
footer {
  display: grid;
  padding: min(3rem, 5%);
}